body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100%;
    height: 350px;
    background-image: url(/clickygame/static/media/nickback2.949be400.jpg);
    background-size: 100px 175px;
    border-bottom: 8px solid purple;
  }
  
  .tophead {
    width: 100%;
    height: 100px;
    background-color: purple;
    margin: 0;
    position: fixed;
    top: 0;
  }
   .resulttxt {
    position: absolute;
    left: 5%;
    font-size: 40px;
  }
  
  .tscore {
    font-size: 32px;
    font-weight: bolder;
    position: absolute;
    right: 4%;
  }
  
  .score {
    border-right: 2px solid white;
    font-size: 32px;
    font-weight: bolder;
    position: absolute;
    right: 20%;
    padding-right: 30px;
  }
  
  .bottomhead {
    text-align: center;
  }
  
  .btitle {
    font-weight: bolder;
    font-size: 62px;
    color: purple;
    margin-top: 100px;
  }
  
  .binstruct {
    font-weight: bolder;
    font-size: 42px;
    color: purple;
  }
   #s {
    margin-left: 3px;
  }
   #ts {
    margin-left: 3px;
  }
.nickpics {
    width: 150px;
    height: 150px;
    border: 4px solid purple;
    float: left;
    margin: 10px;
  }
.wrapper {
    width: 750px;
    height: 560px;
    margin: 0 auto;
  } 
.footer {
    text-align: center;
    width: 100%;
    background-color: purple;
    height: 60px;
    padding: 10px;
  }
